<template>
  <v-container fluid class="fill-height hero">
    <v-row
      ><v-col class="d-flex justify-center">
        <div>
          <v-img
            class="mx-auto my-6"
            max-width="228"
            src="../assets/fitoagro_logo.png"
          ></v-img>
          <v-card
            class="mx-auto pa-12 pb-8"
            elevation="8"
            width="28rem"
            rounded="lg"
          >
            <v-card-text>
              <v-form v-model="formValid">
                <v-row>
                  <v-col cols="12">
                    <v-alert color="primary" variant="tonal">
                      Será enviado um email com instruções para alterar a senha.
                    </v-alert>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      v-model.trim="email"
                      :rules="rules.email"
                      placeholder="Insira o email associado à conta"
                      prepend-inner-icon="mdi-email-outline"
                      color="primary"
                      density="compact"
                      variant="outlined"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-btn
                      block
                      size="large"
                      color="primary"
                      variant="tonal"
                      :loading="isLoading"
                      :disabled="!formValid"
                      @click="sendEmail()"
                      >Alterar senha</v-btn
                    >
                  </v-col>
                </v-row>
              </v-form>
            </v-card-text>
          </v-card>
        </div>
      </v-col></v-row
    >
  </v-container>
</template>

<script>
import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";

export default {
  name: "ForgotPasswordView",
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();
    return { generalStore, accountStore };
  },
  data() {
    return {
      email: "",
      rules: {
        email: [
          (v) => !!v || "O email é obrigatório",
          (v) => /.+@.+\..+/.test(v) || "O email deve ser válido",
        ],
      },
      formValid: false,
      isLoading: false,
    };
  },
  methods: {
    async sendEmail() {
      this.isLoading = true;
      await axios
        .post("api/forgotPassword", { email: this.email })
        .then((response) => {
          this.isLoading = false;
          this.generalStore.successSnackbar(
            "Email enviado com instruções para alterar a senha"
          );
          this.$router.push("/login");
        })
        .catch((error) => {
          this.isLoading = false;
          console.log(error);
          this.generalStore.errorSnackbar(error.response.data.error);
        });
    },
  },
};
</script>

<style scoped>
.hero {
  background: url("../assets/crops_bg.jpg") no-repeat center center;
  background-size: cover;
}
</style>
